export default {
	data() {
		return {
			roomIndex:0,
			address:"",
			shopname:"",
			mini_price:"",
			ytime:"",
			phone:"",
			ishave:"",
			slideshow:"",
			house_info:'',
			allow_order_house:"",
			lat:"",
			long:'',
			code:'',
			sid:''
		}
	},
	onLoad(e) {
		var sid = e.id
		this.long = e.x
		this.lat = e.y
		this.sid = sid
		this.hotelHouse(sid)
		this.shouJi()
	},
	onShow() {
		this.shouJi()
	},
	onPullDownRefresh() {
	    this.refresh()
	},
	methods: {
		async hotelHouse(id){
			uni.showLoading({})
			const that = this
			const res = await this.$myRequest({
				method:"POST",
				url:"/houseDetail?sid="+ id
			})
			uni.hideLoading()
			that.address = res.data.shop_info.address
			that.shopname = res.data.shop_info.shopname
			that.mini_price = res.data.shop_info.mini_price
			that.ytime = res.data.shop_info.ytime
			that.ishave = res.data.shop_info.ishave
		    that.phone = res.data.shop_info.phone
			that.slideshow =  res.data.door_photos
			that.house_info = res.data.house_info
			that.allow_order_house = res.data.allow_order_house
		},
		reserve(e){
			const code = this.code
			const token = uni.getStorageSync("token")
			const phone = uni.getStorageSync("phone")
			const id = e.id
			const status = e.status
			if(!token){
				uni.navigateTo({
					url:"../login/login"
				})
				return
			}
			else if(code ==0){
				uni.navigateTo({
					url:"../huo_phone/huo_phone"
				})
			}
			else if(status == 0){
				uni.navigateTo({
					url:"../make_room_reservation/make_room_reservation?id=" +id
				})
			}else{
				uni.showToast({
					title:"该房间暂未开放"
				})
			}
		},
		phones(){
			uni.makePhoneCall({
				phoneNumber:this.phone
			})
		},
		addressName(){
			const address = this.address
			const  lat= parseFloat(this.lat)
			const  long= parseFloat(this.long)
			uni.openLocation({
				latitude:lat,
				longitude:long,
				scale: 18,
				name:address
			})
		},
		async shouJi(){
			const that = this
			const token = uni.getStorageSync("token")
			const uid = token.uid
			const res = await this.$myRequest({
				method:"POST",
				url:"/checkMobile",
				data:{
					uid:uid
				},
			})
			that.code = res.data.code
		},
		refresh(){
			setTimeout(()=>{
				this.hotelHouse(this.sid)
				uni.stopPullDownRefresh()
			},2000)
		}
	}
}
